* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
body{
  background-image: url(../src/img/pexels-lukas-1419923.jpg);
  background-size:cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

html {
  font-size: 62.5%;
  /* font-family: 'Exo 2', sans-serif; */
  font-family: 'Montserrat', sans-serif;
}

.task-content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 38rem;
  text-align: center;
  border: 1px solid;
  padding: 1rem;
  border-radius: .8rem;
  box-shadow:  2px 2px 4px #000000;
  background-color: rgb(234, 234, 237);
}

.main-title {
  letter-spacing: .5rem;
  font-weight: 600;
  font-size: 3rem;
}


.input-button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;

}

.createTodo-input {
  width: 20rem;
  border-radius: .5rem;
}

.task-content input {
  height: 2.2rem;
  padding: .15rem;
}

.task-content input:focus {
  outline: none;
  border: 2px solid #1DA1F2;
}

.submitTodo-button {
  box-shadow: 2px 3px 3px 2px  rgb(115, 115, 115, 0.75);
  width: fit-content;
  height: 2.25rem;
  font-weight: 600;
  letter-spacing: .2rem;
  padding: .15rem;
  cursor: pointer;
  margin-left: .5rem;
  border-radius: .5rem;
  background-color: #1DA1F2;
  border: 1px solid #3a3838;
  color: #ffffff;
  transition: all .2s;
}

.submitTodo-button:active {
  transform: scale(0.98);
  box-shadow: 1px 1.5px 1.5px rgba(0, 0, 0);
}

.submitTodo-button:hover {
  background-color:rgba(6, 91, 248, 0.75);
  transition: all .2s
}

.item-container{
  margin-bottom: 1.25rem;
  background-color: transparent;
  border-bottom: 1px solid transparent;
  border-image: linear-gradient(to right,rgba(255, 255, 255, 0.1),rgb(29, 161, 242), rgba(255, 255, 255, 0.1));
  border-image-slice: 20;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}

.item-checkbox {
  margin-left: 1rem;
  height: 2rem;
  width: 2rem;
}

.item-container input {
  cursor: pointer;
}

.item-container input {
  font-size: 1.8rem;
  background-color: transparent;
  border: none;
}

.item-container input:focus{
  border: none;
}


.completed-todo {
  text-decoration: line-through 1.75px #1DA1F2;
  background-color: lightslategray;
  border-radius: 5px;
  position: relative;
  opacity: 0.7;
}


ul {
  list-style-type: none;
  padding-top: 1.5rem;
}


svg {
  padding: 0 1rem 0 1rem;
  height: 2rem;
  width: 2rem;
  color: rgb(166, 36, 36);
  
}

svg:hover {
  color:rgb(237, 5, 5);
  cursor: pointer;
  transform: scale(1.1);
  transition: all .3s;
}

.main-content-container{
  margin-top: 12rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
}



